@font-face {
  font-family: "Cafe24Oneprettynight";
  src: local("Cafe24Oneprettynight"), url('./fonts/Cafe24Oneprettynight.ttf') format('truetype');
  font-weight: auto;
}

.App, body {
  //max-width: 360px;
  margin: auto;
  font-family: 'Cafe24Oneprettynight', sans-serif;
  //overflow: hidden;
}

div {
  //text-align: center;
  .section {
    width: 100vw;
    height: 100vh;
  }
}

img {
  width: 100%;
}

.invitation {
  position: relative;

  .title {
    position: absolute;
    top: 91px;
    left: 44px;

    .icon {
      width: 26.34px;
      height: 12.02px;
      margin-bottom: 18px;
    }

    .wedding {
      font-size: 20px;
      line-height: 21px;
      letter-spacing: 0.3em;
      font-weight: 400;
      margin-bottom: 14px;
    }

    .names {
      display: flex;
      flex-grow: 0;

      p {
        font-size: 29px;
        line-height: 31px;
        letter-spacing: 0.3em;

        &:nth-child(2) {
          margin-right: 10px;
        }
      }

      margin-bottom: 41px;
    }

    .schedule {
      font-size: 17px;
      line-height: 19px;
      letter-spacing: 0.12em;
      font-weight: 400;
    }
  }

  .illust {
    width: 235.82px;
    height: 335.97px;
    position: absolute;
    bottom: 20px;
    right: 0;
  }
}

.location {
  background-color: #F9EAEC;

  .contents {
    padding-top: 70px;
  }

  .main-title {
    color: #604347;
    font-size: 26px;
    line-height: 28px;
    text-align: center;
    margin-bottom: 16px;
  }

  .hotel-address {
    font-size: 20px;
    text-align: center;
    line-height: 28px;
    margin-bottom: 24px;
  }

  .route {
    display: flex;
    justify-content: center;
    gap: 32px;
    margin-bottom: 32px;

    a {
      img {
        width: 56px;
        height: 56px;
        background-color: #ffffff;
        border-radius: 10px;
      }

      text-align: center;

      p {
        font-size: 16px;
        line-height: 26px;
      }
    }
  }

  .map {
    //margin-bottom: 39px;
  }

  .sub-title {
    background-color: #F0DBDE;
    border-radius: 9px;
    padding: 2px 20px;
    width: 70px;
    text-align: center;
    margin: 0 auto 16px auto;
  }

  .parking-address {
    font-size: 16px;
    line-height: 23px;
    text-align: center;
    margin-bottom: 16px;
  }
}

.film {
  .contents {
    padding-top: 100px;
  }

  .title {
    font-size: 26px;
    line-height: 28px;
    text-align: center;
    color: #604347;
    margin-bottom: 37px;
  }

  .swiper-container {
    position: relative;

    .bg {
      position: absolute;
      width: 100vw;

      img {
        position: absolute;
        width: 100%;

        &:nth-child(1) {
          top: -20px;
        }
      }
    }

    .swiper-wrapper {
      margin-top: 46px;

      img {
        border-radius: 20px;
      }
    }
  }
}

.gallery {
  .contents {
    padding-top: 100px;

  }
  .title {
    font-size: 26px;
    line-height: 28px;
    text-align: center;
    color: #604347;
    margin-bottom: 37px;
  }

  .item-wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  .item {
    flex: 0 0 33.3333333%;
    height: 15vh;
    overflow: hidden;
    margin-bottom: 20px;

    img {
      width: 80%;
      display: block;
      margin: auto;
    }
  }
}

.information {
  .contents {
    padding-top: 100px;
  }
  .title {
    font-size: 26px;
    line-height: 28px;
    color: #604347;
    text-align: center;
    margin-bottom: 27px;
  }
}
